.cat-image {
  width: 200px;
  height: auto;
  margin-top: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}

.hidden {
  display: none;
}

.loader {
  height: 150px;
  width: 100px;
  background: #ff3d00 linear-gradient(0deg, #f63a99 25%, #30dcf6 25%, #30dcf6 25%, #30dcf6 50%, #f2d200 50%, #f2d200 50%, #f2d200 75%, #70ca5c 75%) 0 0 / auto 175px repeat-y;
  border-radius: 55px 55px 10px 10px;
  margin-top: 15%;
  margin-left: 45%;
  animation: colorShift 6s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 15px;
  height: 100px;
  background: rgba(255, 255, 255, .5);
  border-radius: 50px;
  position: absolute;
  bottom: 15px;
  left: 10px;
}

.loader:after {
  content: "";
  width: 32px;
  height: 45px;
  background: #e09c5f;
  border-radius: 0 0 12px 12px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%);
  box-shadow: inset 0 15px 2px rgba(0, 0, 0, .25);
}

@keyframes colorShift {
  to {
    background-position: 0 175px;
  }
}

.temper {
  font-size: 16px;
  font-weight: 600;
}

.cat-info {
  gap: 20px;
  display: flex;
}

